import { DatePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { IUser } from 'src/app/shared/interfaces/i-auth';
import { ICompany } from 'src/app/shared/interfaces/i-company';
import { IRol } from 'src/app/shared/interfaces/i-rol';
import { NotificationService } from '../../../services/notification.service';
import { RolService } from '../../../services/rol.service';
import { CompanyService } from '../../company/services/company.service';
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-user-dialog',
  templateUrl: './user-dialog.component.html',
  styleUrls: ['./user-dialog.component.scss']
})
export class UserDialogComponent implements OnInit {

  userForm!: FormGroup;
  manyUsersForm!: FormGroup;
  userData!: IUser;
  companys!: ICompany[];
  roles!:    IRol[];
  userInvalid: Boolean;
  manyUsersInvalid: Boolean;
  formData = new FormData();
  filePath: string ="";

  constructor(public dialogRef: MatDialogRef<UserDialogComponent>,
              @Inject(MAT_DIALOG_DATA) public data: any,
              private userSvc: UserService,
              private formB: FormBuilder,
              private notiSvc: NotificationService,
              private companySvc: CompanyService,  
              private rolSvc:     RolService,
              public datepipe:    DatePipe) { 
                this.buildForm();
              }

  ngOnInit(): void {
    this.getCompanies();
    this.getRoles();
    if(this.data?.element){
      this.userForm.patchValue(this.data.element)
    }
  }

  submitUser(): void {
    if(this.userForm.invalid){
      /*this.notiSvc.openSnackBar("Algunos de los campos son invalidos", 3000)
      return*/
      this.userInvalid = true
    } 
    if ( this.manyUsersForm.invalid){
      this.manyUsersInvalid = true
    }
    if (this.userInvalid && this.manyUsersInvalid){
      this.notiSvc.openSnackBar("Algunos de los campos son invalidos",2000)
      return
    } else{
      this.setUser()
      if(this.data.element){
        this.userSvc.updateUser(this.userData).subscribe(
          val => {
            console.log(val)
            this.notiSvc.openSnackBar("Usuario actualizado correctamente", 2000)
            this.dialogRef.close()
          },
          error => {
            this.notiSvc.openSnackBar("Hubo un error al momendo de actualizar el usuario", 2000)
            console.log(error)
          }
        )
      } else {
        if(!this.userInvalid){
          this.userSvc.createSingleUser(this.userData).subscribe(
            val => {
              console.log(val)
              this.notiSvc.openSnackBar("Usuario agregado correctamente", 2000)
              this.dialogRef.close()
            },
            error => {
              this.notiSvc.openSnackBar("Hubo un error al momendo de agregar el usuario", 2000)
              console.log(error)
            }
          )
        } else {
          this.setManyUsers()
          this.userSvc.createManyUsers(this.formData).subscribe(
            val => {
              console.log(val)
              this.notiSvc.openSnackBar("Lista de usuarios creada correctamente", 2000)
              this.dialogRef.close()
            },
            error => {
              this.notiSvc.openSnackBar("Hubo un error al momento de crear los usuarios",2000)
              console.log(error)
            }
          )
        }
      }
    }
  }

  onChange(e) {
    if (e.target.files){
      var reader = new FileReader()
      reader.readAsDataURL(e.target.files[0])
      reader.onload = (event : any) => {
        this.filePath = (<HTMLInputElement>document.getElementById('file')).files[0].name
        console.log(this.filePath)
      }
    }
  }

  getCompanies(): void {
    this.companySvc.showCompanies().subscribe((data) => {
      this.companys = data['data']
    })
  }

  getRoles(): void {
    this.rolSvc.showRoles().subscribe((data) => {
      this.roles = data.data
      const found = this.roles.find(element => element.name == "Invitado")
      var index = this.roles.indexOf(found)
      if (index > -1) {
          this.roles.splice(index, 1);
      }
    })
  }


  setUser(): void {
    if(this.data.element){
      let email:string;
      if(this.data.element.email == this.userForm.get('email')?.value){
        email = null;
      }else {
        email = this.userForm.get('email')?.value
      }
      console.log(email)
      this.userData = {
        id: this.data?.element ? this.data?.element.id : null,
        name: this.userForm.get('name')?.value,
        email: this.userForm.get('email')?.value.trim()/*email*/,
        last_name: this.userForm.get('last_name')?.value,
        last_name2: this.userForm.get('last_name2')?.value,
        enterprise_id: this.userForm.get('enterprise_id')?.value,
        birthdate: this.datepipe.transform(this.userForm.get('birthdate')?.value, 'yyyy/MM/dd'),
        premium: this.userForm.get('premium')?.value,
        phone: this.userForm.get('phone')?.value,
      }

    } else{
      this.userData = {
        id: this.data?.element ? this.data?.element.id : null,
        name: this.userForm.get('name')?.value,
        email: this.userForm.get('email')?.value,
        last_name: this.userForm.get('last_name')?.value,
        last_name2: this.userForm.get('last_name2')?.value,
        enterprise_id: this.userForm.get('enterprise_id')?.value,
        birthdate: this.datepipe.transform(this.userForm.get('birthdate')?.value, 'yyyy/MM/dd'),
        premium: this.userForm.get('premium')?.value,
        phone: this.userForm.get('phone')?.value,
        role_id: this.userForm.get('role_id')?.value,
      }
    }
  }

  setManyUsers():void{
    this.formData.append('enterprise_id', this.manyUsersForm.get('enterprise_id')?.value),
    this.formData.append('file', (<HTMLInputElement>document.getElementById('file')).files[0])
  }

  buildForm(): void {
    if(this.data.element){
      this.userForm = this.formB.group({
        name: ['', Validators.required],
        last_name: ['', Validators.required],
        last_name2: ['',],
        email: ['', Validators.required],
        enterprise_id: ['', Validators.required],
        birthdate: [''],
        premium: ['', Validators.required],
        phone: ['', Validators.required],
        role_id: ['']
    })
    } else {
      this.userForm = this.formB.group({
        name: ['', Validators.required],
        last_name: ['', Validators.required],
        last_name2: ['',],
        email: ['', Validators.required],
        enterprise_id: ['', Validators.required],
        birthdate: ['',Validators.required],
        premium: ['', Validators.required],
        phone: ['', Validators.required],
        role_id: ['', Validators.required]
      })
    }

    this.manyUsersForm = this.formB.group({
      enterprise_id: ['', Validators.required],
      file: ['', Validators.required]
    })
  }
}
