import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Form } from '@angular/forms';
import { Observable } from 'rxjs';
import { IUser, IUserRes } from 'src/app/shared/interfaces/i-auth';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  constructor(private http: HttpClient) { }

  showUsers(): Observable<IUser[]>{
    return this.http.get<IUser[]>(`${environment.API_URL}/v1/users`)
  }

  createSingleUser(data: IUser): Observable<IUserRes>{
    return this.http.post<IUserRes>(`${environment.API_URL}/v1/users/single`,data)
  }

  updateUser(data: IUser): Observable<IUserRes>{
    return this.http.patch<IUserRes>(`${environment.API_URL}/v1/users/${data.id}`,data)
  }
  
  createManyUsers(data: FormData): Observable<IUser[]>{
    return this.http.post<IUser[]>(`${environment.API_URL}/v1/users/many`,data)
  }
}
