import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort, Sort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { IUser } from 'src/app/shared/interfaces/i-auth';
import { UserService } from './services/user.service';
import { UserDialogComponent } from './user-dialog/user-dialog.component';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss']
})
export class UserComponent implements OnInit {
  displayedColumns: string[] = ['id', 'nombre','last_name', /*'last_name2',*/'birthdate','email'/*,'phone'*/, 'premium','edit'];
  dataSource: MatTableDataSource<IUser>;
  user!: IUser;
  users: IUser[]

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  sortedData!: IUser[];

  constructor(private userSvc: UserService,
              public dialog: MatDialog) {
  }

  ngOnInit(): void {
    this.getUsers()
  }

  openDialog(edit: boolean, element?: IUser): void {
    const dialogRef = this.dialog.open(UserDialogComponent, {
      disableClose: false,
      data: {edit, element},
    }).afterClosed().subscribe((data: any) => {
      if(!data){
        this.getUsers();
      }
    });
  }

  getUsers(): void {
    this.userSvc.showUsers().subscribe(
      val => {
        this.users = val['data']
        this.setData()
      },
      error => {
        console.log(error)
      }
    )
  }


  setData(): void {
    this.dataSource = new MatTableDataSource();
    this.dataSource.data = this.users;
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }


  applyFilter(event: Event) {
      const filterValue = (event.target as HTMLInputElement).value;
      this.dataSource.filter = filterValue.trim().toLowerCase();

      if (this.dataSource.paginator) {
        this.dataSource.paginator.firstPage();
      }
    }
  
  selecteRow(user: IUser) {
      this.user = user
  }
}

